/* Layouts: Header */

.header {
  
}
  .header-top {
    
  }
  .header-middle {
    background-color: var(--bs-warning);
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
    .header-left,
    .header-right {
      flex-grow: 1;
      flex-basis: 100%;
    }
    .header-left {
      text-align: left;
    }
    .header-brand {
      text-decoration: none;

      border: 0.25rem solid var(--bs-dark);
      color: var(--bs-dark);
      font-size: 1.5rem;
      font-weight: 700;

      padding: 0.5rem 1.5rem;

      text-transform: uppercase;
    }
      .header-brand:hover {
        color: var(--bs-dark);
      }
    .header-right {
      text-align: right;
    }
  .header-bottom {
    background-color: var(--bs-warning);
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }


/* Nav header */
.nav-header {
  
}
.nav-header .nav-link {
  position: relative;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
}
.nav-header .nav-link::before {
  content: '';
  position: absolute;
  left: 0; bottom: 0;
  width: 0;
  height: .25rem;
  background-color: var(--bs-dark);
  transition: width 0.25s ease-in-out;
}
.nav-header .nav-link.active::before,
.nav-header .nav-link:hover::before {
  width: 100%;
}
/* Layouts: Row */
.row-8 {
  margin-right: -0.5rem;
  margin-left: -0.5rem;
}
.row-8 > [class^="col"] {
  padding-right: 0.5rem;
  padding-left: 0.5rem;
}

/* Buttons */

.btn {
  font-weight: 400;
  text-transform: uppercase;
}
.btn,
.btn-md {
  font-size: .875rem;
}

.btn-icon {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  padding-right: 0;
  padding-left: 0;
  line-height: 1.6;
}

